"use client";

import { useState } from "react";
import { useAccount, useBalance, useSendTransaction } from "wagmi";
import { parseEther } from "viem";
import { Button } from "@rainbow-me/rainbowkit";

const DONATION_ADDRESS = "0xYourDonationWalletAddress"; // Ganti dengan wallet tujuan donasi

export default function DonationForm() {
  const { isConnected, address } = useAccount();
  const [amount, setAmount] = useState("");
  const [txHash, setTxHash] = useState("");
  const [error, setError] = useState("");

  const { sendTransaction, isLoading } = useSendTransaction();

  const handleDonate = async () => {
    setError("");
    setTxHash("");

    if (!amount || isNaN(Number(amount))) {
      setError("Masukkan jumlah yang valid.");
      return;
    }

    try {
      const parsedAmount = parseEther(amount);

      const result = await sendTransaction({
        to: DONATION_ADDRESS,
        value: parsedAmount,
      });

      setTxHash(result?.hash ?? "Transaksi terkirim");
    } catch (err: any) {
      setError(err.message || "Terjadi kesalahan");
    }
  };

  if (!isConnected) {
    return (
      <div className="p-4 bg-yellow-100 text-yellow-800 rounded-md">
        Hubungkan wallet terlebih dahulu untuk berdonasi.
      </div>
    );
  }

  return (
    <div className="p-6 bg-white rounded-xl shadow-md max-w-md w-full">
      <h2 className="text-xl font-semibold mb-4">Form Donasi Crypto</h2>
      <input
        type="number"
        placeholder="Jumlah ETH"
        value={amount}
        onChange={(e) => setAmount(e.target.value)}
        className="w-full border px-4 py-2 rounded-lg mb-3"
      />
      <button
        onClick={handleDonate}
        disabled={isLoading}
        className="w-full bg-red-600 text-white font-bold py-2 rounded-lg hover:bg-red-700 transition"
      >
        {isLoading ? "Mengirim..." : "Kirim Donasi"}
      </button>

      {txHash && (
        <div className="mt-3 text-green-600 break-all">
          ✅ Transaksi berhasil: {txHash}
        </div>
      )}

      {error && <div className="mt-3 text-red-600">❌ Error: {error}</div>}
    </div>
  );
}
